.moon-svg {
  position: absolute;
  width: 30px;
  top: 10px;
  left: 85px;
  z-index: 100;
  fill: #7e7e7e;
  transition: 0.3s;
}

.sun-svg {
  position: absolute;
  width: 30px;
  top: 10px;
  left: 10px;
  z-index: 100;
  fill: #fff;
  transition: 0.3s;
}

.theme-container {
  padding: 1rem;
}

.theme-lable {
  width: 125px;
  height: 50px;
  position: relative;
  display: block;
  top: -5;
  border-radius: 50px;
  box-shadow:
    inset 0 5px 15px rgba(0, 0, 0, 0.4),
    inset 0 -5px 15px rgba(255, 255, 255, 0.4);
  cursor: pointer;
}

.theme-lable::after {
  content: '';
  width: 45px;
  height: 45px;
  position: absolute;
  top: 3;
  left: 10;
  background: linear-gradient(180deg, #ffcc89, #d8860b);
  border-radius: 100%;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  transition: 0.3s;

}

.theme-input {
  display: none;
}

.theme-input + label {
  background: rgb(185, 181, 181);
}

.theme-input:checked + label {
  background: rgb(74, 74, 74);
}

.theme-input:checked svg {
  display: none;
}

.sun-svg {
  opacity: 0;
}

.theme-input:checked + .sun-svg {
  opacity: 1;
}

.theme-input:checked + label::after {
  content: '';
  left: 120px;
  transform: translateX(-50px);
  background: linear-gradient(180deg, #777, #3a3a3a);
}
